import React, { useContext } from 'react';
import dayjs from 'dayjs';
import { Markup } from 'interweave';
import { AuthContext } from '../../lib/context/auth-context';
import TokenAPI from '../../lib/api/token';

const Text = (props) => {
  const { dadosUser } = useContext(AuthContext);
  const storage = TokenAPI.getToken();
  const user = dadosUser || storage;
  const day = dayjs().format('DD');
  const month = dayjs().month();
  const year = dayjs().year();

  const { data } = props;

  const formataMonth = (datas) => {
    let dayOfWeek;
    switch (datas) {
      case 0:
        dayOfWeek = 'Janeiro';
        break;
      case 1:
        dayOfWeek = 'Fevereiro';
        break;
      case 2:
        dayOfWeek = 'Maro';
        break;
      case 3:
        dayOfWeek = 'Abril';
        break;
      case 4:
        dayOfWeek = 'Maio';
        break;
      case 5:
        dayOfWeek = 'Junho';
        break;
      case 6:
        dayOfWeek = 'Julho';
        break;
      case 7:
        dayOfWeek = 'Agosto';
        break;
      case 8:
        dayOfWeek = 'Setembro';
        break;
      case 9:
        dayOfWeek = 'Outubro';
        break;
      case 10:
        dayOfWeek = 'Novembro';
        break;
      case 11:
        dayOfWeek = 'Dezembro';
        break;
      default:
        break;
    }
    return dayOfWeek;
  };
  const FormatarTermo = () => {
    // const day = dadosUsers.data.substr(0, 2);
    // const month = dadosUsers.data.substr(3, 5);
    // const year = dadosUsers.data.substr(6, 9);
    const mes = formataMonth(month);
    let term = data?.replace('%name', user.name);
    term = term?.replace('%name', user.name);
    term = term?.replace('%matricula', user.matricula);
    term = term?.replace('%matricula', user.matricula);
    term = term?.replace('%dia', day);
    term = term?.replace('%mes', mes);
    term = term?.replace('%ano', year);

    return term;
  };

  return (
    <div id="text-term">
      <Markup content={FormatarTermo()} />
      {/* <p>
        Eu, <b>%name</b>, matrcula funcional n <b>%matricula</b>, declaro ter
        cincia da obrigatoriedade do cumprimento das regulamentaes descritas
        no presente termo, assumindo total responsabilidade pela correta
        utilizao do Sistema Eletrnico de Controle de Refeies da Unimed
        Chapec - Cooperativa de Trabalho Mdico da Regio Oeste Catarinense,
        mediante as consideraes e condies abaixo descritas:
      </p>
      <p>
        Considerando que a Unimed Chapec  inscrita no PAT - Programa de
        Alimentao do Trabalhador, mantendo servios prprios de fornecimento
        de refeies;
      </p>
      <p>
        Considerando que a Unimed Chapec oferece cardpio/refeio aos seus
        colaboradores de acordo com a jornada de trabalho e respeitando disposto
        nas normas internas da Cooperativa;
      </p>
      <p>
        {' '}
        Considerando que as refeies, desde que realizadas de acordo com o
        cardpio oferecido (lanche da manh, almoo, lanche da tarde e jantar),
        conforme jornada de trabalho realizada nas dependncias do refeitrio da
        Unimed Chapec, custaro ao() colaborador(a) 20% (vinte por cento) do
        valor total da refeio;
      </p>
      <p>
        Considerando que o consumo de refeies/itens no previstos no cardpio
        oferecido pela Unimed Chapec, no  subsidiado, e, portanto, custaro
        ao() colaborador(a) 100% (cem por cento) do valor correspondente ao
        total da refeio/item adquirido;
      </p>
      <p>
        Considerando que a Unimed Chapec realiza o controle das refeies/itens
        consumidos atravs do Sistema Eletrnico de Controle de Refeio;
      </p>
      <p>
        Considerando que o lanamento em sistema das refeies/itens consumidos
        e seus respectivos valores,  realizado por colaborador do Setor de
        Nutrio da Unimed Chapec no momento do consumo e validado atravs da
        identificao digital do(a) colaborador(a)consumidor(a);
      </p>

      <p>
        Considerando que os lanamentos de refeies/itens podero ser acessados
        pelo(a) Colaborador(a) atravs do portal Intranet - Sistema Eletrnico
        de Controle de Refeio, atravs de login e senha individual, sigilosa e
        intransfervel;
      </p>
      <p>
        <b>CLUSULA PRIMEIRA  </b>
        Pelo presente termo de responsabilidade e utilizao, comprometo-me a:
      </p>
      <p>
        <b>Pargrafo Primeiro - </b>Utilizar o sistema eletrnico de forma
        correta, registrando toda e qualquer refeio e validando-a atravs de
        identificao digital no momento do efetivo consumo.
      </p>
      <p>
        <b>Pargrafo Segundo - </b>Apresentar crach de identificao ao
        colaborador do Setor de Nutrio no caso de impossibilidade de
        utilizao do sistema de identificao digital, possibilitando o correto
        registro da refeio/item consumido atravs da matrcula funcional.
      </p>
      <p>
        <b>Pargrafo Terceiro - </b>Informar corretamente minha matricula
        funcional quando a solicitao de refeies ocorrer de forma eletrnica
        atravs do preenchimento de formulrio online.{' '}
      </p>
      <p>
        <b>Paragrafo Quarto - </b>Conferir no momento do consumo e/ou de forma
        digital atravs do Sistema Eletrnico de Controle de Refeio, os
        lanamentos efetuados pelo colaborador do Setor de Nutrio e validados
        atravs da identificao digital e/ou matricula funcional.{' '}
      </p>
      <p>
        <b>Paragrafo Quinto - </b>Contatar imediatamente o Setor de Nutrio,
        quando identificado algum lanamento indevido e/ou incorreto, para a
        devida conferncia e alterao caso necessrio.{' '}
      </p>
      <p>
        <b>CLUSULA SEGUNDA  </b>Declaro estar ciente e autorizo o desconto em
        folha de pagamento do valor correspondente a todas as refeies/itens
        lanados no sistema de Sistema Eletrnico de Controle de Refeio,
        relativos s refeies constantes no cardpio da Unimed Chapec de
        acordo com as normas internas e jornada de trabalho (subsidiadas), bem
        como dos itens/refeies no previstos no mencionado cardpio (no
        subsidiados) e por mim consumidos.{' '}
      </p>
      <p>
        <b>Pargrafo Primeiro  </b>O perodo de apurao das despesas
        relacionadas aos consumos realizados no setor de nutrio e lanados no
        Sistema Eletrnico de Controle de Refeio, ocorrer entre os dias 26 ao
        dia 25 de cada ms, sendo minha responsabilidade realizar a conferncia
        e aceite dos itens lanados, impreterivelmente, at o dia 27 de cada
        ms, independentemente do dia da semana, no sendo possvel a realizao
        da conferncia aps a referida data.
      </p>
      <p>
        <b>Paragrafo Segundo - </b>Decorrido o prazo para conferncia dos
        lanamentos, o valor total do consumo lanado no respectivo ms ser
        descontado em folha de pagamento, independentemente da realizao da
        conferncia/aceite.
      </p>
      <p>
        <b>Paragrafo Terceiro  </b>Em casos de afastamento temporrio por
        motivos de frias, auxlio doena, auxlio maternidade/ paternidade ou
        qualquer outro, minha identificao digital ser bloqueada, no sendo
        possvel a realizao de refeies e/ou quaisquer outros consumos nas
        dependncias do refeitrio da Unimed Chapec. Nada mais havendo e ciente
        das responsabilidades pelas obrigaes estipuladas e autorizaes
        concedidas, firmo o presente termo.
      </p>
      <p className="text-center"> Chapec/SC, %dia de %mes de %ano.</p>
      <p className="text-center" style={{ marginBottom: '-18px' }}>
        {' '}
        %name
      </p> */}
    </div>
  );
};

export default Text;
